import React, { useEffect, useState } from "react";
import { useParams, Redirect, useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useMutation } from "@apollo/client";
import Alert from "@material-ui/lab/Alert";

import css from "./Invitation.module.css";
import LoadingSpinner from "@components/LoadingSpinner";
import {
  CONFIRM_INVITATION_TOKEN,
  REGISTER_COMPANY,
  REGISTER_PRACTICE_SUPERVISER,
  REGISTER_STUDENT,
} from "./queries";
import useSnackGraphql from "@hooks/useSnackGraphql";
import Input from "@components/Input";
import USER_TYPES from "@config/userTypes";
import Logo from "@assets/Logo";
import Statement from "@components/Statement";
import useAuth from "@hooks/useAuth";

const Invitation = () => {
  const { token } = useParams();
  const history = useHistory();
  const [getInvitationConfirm, { loading }] = useMutation(
    CONFIRM_INVITATION_TOKEN
  );
  const [invitationError, setInvitationError] = useState(false);
  const [registerStudent] = useMutation(REGISTER_STUDENT);
  const [registerCompany] = useMutation(REGISTER_COMPANY);
  const [registerPracticeSuperviser] = useMutation(
    REGISTER_PRACTICE_SUPERVISER
  );
  const [invitationData, setInvitationData] = useState(null);
  const [registerSuccess, setRegisterSuccess] = useState(false);
  const [enqueueError, enqueueSnackbar] = useSnackGraphql();
  const { register, handleSubmit, watch, errors } = useForm();
  const { removeToken, removeUserType } = useAuth();

  const succesRegister = () => {
    enqueueSnackbar("Zarejestrowano pomylnie", {
      variant: "success",
    });

    setRegisterSuccess(true);

    setTimeout(() => {
      removeToken();
      removeUserType();
      history.push("/login");
    }, 5000);
  };

  const onSubmit = (data) => {
    if (invitationData) {
      const { userType } = invitationData;
      switch (userType) {
        case USER_TYPES.student:
          registerStudent({
            variables: {
              ...data,
              index_number: parseInt(data.index_number),
              token,
            },
          })
            .then((data) => {
              data && succesRegister();
            })
            .catch((error) => {
              enqueueError(error);
            });
          break;
        case USER_TYPES.company:
          registerCompany({ variables: { ...data, token } })
            .then((data) => {
              data && succesRegister();
            })
            .catch((error) => {
              enqueueError(error);
            });
          break;
        case USER_TYPES.practiceSuperviser:
          registerPracticeSuperviser({ variables: { ...data, token } })
            .then((data) => {
              data && succesRegister();
            })
            .catch((error) => {
              enqueueError(error);
            });
          break;
        default:
          console.log("UserType error");
      }
    }
  };

  useEffect(() => {
    if (token) {
      getInvitationConfirm({ variables: { token } })
        .then(({ data }) => {
          console.log(data);
          if (data.confirmInvitationToken) {
            setInvitationData(data.confirmInvitationToken);
          }
        })
        .catch((err) => {
          setInvitationError(true);
          enqueueError(err);
        });
    }
  }, [token]);

  if (loading) return <LoadingSpinner />;
  if (registerSuccess) {
    return (
      <>
        <Statement title="Zarejestrowano pomylnie" isLoading={true}>
          Zarejestrowano pomylnie. <br />
          za chwil nastpi przekierowanie do strony logowania.
        </Statement>
      </>
    );
  }

  if (invitationError) {
    return (
      <Statement title="Wystpi bd">
        Link z zaproszeniem wygas, lub zosta wykorzystany, skontaktuj si z
        opiekunem praktyk.
      </Statement>
    );
  }

  return (
    <div className={css.outerContainer}>
      <div className={css.container}>
        <div className={css.header}>
          <h2>Stwrz konto</h2>
          <Logo />
        </div>
        <div className={css.content}>
          <div>
            <label>Adres email</label>
            <br />
            <span>{invitationData?.email}</span>
            <Input
              name="first_name"
              inputRef={register({ required: "Musisz poda imi" })}
              labelOnTop
              label="Imi"
            />
            {errors.first_name && (
              <Alert variant="filled" severity="error">
                {errors.first_name.message}
              </Alert>
            )}
            <Input
              name="last_name"
              inputRef={register({ required: "Musisz poda nazwisko" })}
              labelOnTop
              label="Nazwisko"
            />
            {errors.last_name && (
              <Alert variant="filled" severity="error">
                {errors.last_name.message}
              </Alert>
            )}
            <Input
              name="password"
              type="password"
              inputRef={register({
                required: "Musisz poda haso",
                minLength: {
                  value: 8,
                  message: "Haso musi mie przynajmniej 8 znakw",
                },
              })}
              labelOnTop
              label="Haso"
            />
            {errors.password && (
              <Alert variant="filled" severity="error">
                {errors.password.message}
              </Alert>
            )}
            <Input
              name="confirm_password"
              type="password"
              inputRef={register({
                validate: (value) =>
                  value === watch("password") || "Hasa si nie zgadzaj",
              })}
              labelOnTop
              label="Powtrz haso"
            />
            {errors.confirm_password && (
              <Alert variant="filled" severity="error">
                {errors.confirm_password.message}
              </Alert>
            )}
          </div>
          <div>
            <label>Typ konta</label>
            <br />
            {invitationData?.userType === USER_TYPES.student && (
              <>
                <span>Student</span>
                <Input
                  name="index_number"
                  inputRef={register({
                    required: "Musisz poda numer indeksu",
                  })}
                  labelOnTop
                  label="Numer indeksu*"
                />
                {errors.index_number && (
                  <Alert variant="filled" severity="error">
                    {errors.index_number.message}
                  </Alert>
                )}
              </>
            )}

            {invitationData?.userType === USER_TYPES.company && (
              <>
                <span>Firma</span>
                <Input
                  name="name"
                  inputRef={register({
                    required: "Musisz poda nazw firmy",
                  })}
                  labelOnTop
                  label="Nazwa firmy*"
                />
                {errors.name && (
                  <Alert variant="filled" severity="error">
                    {errors.name.message}
                  </Alert>
                )}
                <Input
                  name="city"
                  inputRef={register({
                    required: "Musisz poda miejscowo i kod pocztowy",
                  })}
                  labelOnTop
                  label="Miejscowo i kod pocztowy*"
                />
                {errors.city && (
                  <Alert variant="filled" severity="error">
                    {errors.city.message}
                  </Alert>
                )}
                <Input
                  name="address"
                  inputRef={register({
                    required: "Musisz poda adres",
                  })}
                  labelOnTop
                  label="Adres*"
                />
                {errors.address && (
                  <Alert variant="filled" severity="error">
                    {errors.address.message}
                  </Alert>
                )}
                <Input
                  name="phone"
                  inputRef={register}
                  labelOnTop
                  label="Telefon"
                />
              </>
            )}
            {invitationData?.userType === USER_TYPES.practiceSuperviser && (
              <>
                <span>Opiekun praktyk</span>
              </>
            )}
          </div>
        </div>
        <div className={css.footer}>
          <button onClick={handleSubmit(onSubmit)}>Stwrz konto</button>
        </div>
      </div>
    </div>
  );
};

export default Invitation;
